//
import oauth_login from '../assets/oauth_login.js';
oauth_login("page_name=home");


import $ from 'webpack-zepto';
import Vue from 'vue';

import cookie from './cookie.js';

//
import set_viewport from '../assets/fontSize.js';
set_viewport();

import focus_swipe from '../assets/focus_swipe.js';
import nav_scroll from '../assets/nav_scroll.js';

//tapjs
import set_active from '../assets/active.js';

import pullup_load from '../assets/pullup_load.js';

/// 
import hot_feed from '../assets/hot_feed.js';
hot_feed();



//loading 
import loading from '../components/loading_ani.vue';
var loading_dialog = new Vue({
    el: '#loading-template',
    render: h => h(loading)
});




//
var locurl = location.href;
if (locurl.indexOf("?") > -1 && !(locurl.indexOf("&") > -1)) {
    var share_url = locurl.replace('?', '?_wv=1');
} else if (!(locurl.indexOf("?") > -1)) {
    var share_url = locurl + '?_wv=1';
} else {
    var share_url = locurl + '&_wv=1';
};
//
$.ajax({
    url: "./wechat/share_index.php",
    type: "get",
    data: {
        'url': location.href.split('#')[0]
    },
    success: function(data) {
        var data = JSON.parse(data);
        //console.log('share_data='+data);
        wx.config({
            debug: false, // ,apialertpclogpc
            appId: data.appId, // 
            timestamp: data.timestamp, // 
            nonceStr: data.nonceStr, // 
            signature: data.signature, // 
            jsApiList: [
                'checkJsApi',
                'onMenuShareTimeline',
                'onMenuShareAppMessage',
                'onMenuShareQQ',
                'onMenuShareWeibo',
                'hideMenuItems'
            ] // JSJS2
        });
        var share_config = {
            title: '', // 
            desc: '', // 
            shareUrl: share_url,
            link: share_url, // 
            imgUrl: 'https://pinquduo.cn/Template/pc/pinquduo/Static/images/pqdlogo.jpg', // 
            type: '', // ,musicvideolinklink
            dataUrl: '', // typemusicvideo
            success: function() {
                //alert('');
            },
            cancel: function() {
                //alert('');
            }
        };

        wx.ready(function() {
            //
            wx.onMenuShareTimeline(share_config);
            //
            wx.onMenuShareAppMessage(share_config);
            //qq
            wx.onMenuShareQQ(share_config);
            //
            wx.onMenuShareWeibo(share_config);
            //
            wx.hideMenuItems({
                //
                menuList: [
                    "menuItem:openWithQQBrowser",
                    "menuItem:openWithSafari",
                    "menuItem:favorite",
                    "menuItem:share:email",
                    "menuItem:share:brand"

                ]
            });
        });
    }
});

//qq
setShareInfo({
    title: '',
    summary: '',
    pic: 'https://pinquduo.cn/Template/pc/pinquduo/Static/images/pqdlogo.jpg',
    url: share_url
});



new Vue({
    el: '#wrap',
    data: function() {
        return {
            //
            home_data: {},
            //
            header_data: [],
            //
            goods_items: [],
            //tab
            cat_goods_data: {
                goods_items: [],
                cat_items: [],
                more_icon_url: 'images/classMore@3x.png'
            },
            hint_timer: null,
            dialog_hint: '',
            hot_banner_url: 'images/hot_banner.jpg',
            //
            links: [{
                    href: 'javascript:;'
                },
                {
                    href: 'free_group.html'
                },
                {
                    href: 'special_haitao.html'
                },
                {
                    href: 'javascript:;'
                },
                {
                    href: 'javascript:;'
                },
                {
                    href: 'special99.html'
                },
                {
                    href: 'special_timed.html'
                },
                {
                    href: 'javascript:;'
                }
            ],
            page: 1,
            pagesize: 8,
            pullup_load: null,
            cat_index: 0,
            isInit: false
        }
    },
    mounted: function() {
        var self_ = this;
        self_.get_header_data();
        self_.get_home_data(self_.page);
        self_.get_cats();


        $('#more-hint').html('...');
        //
        self_.pullup_load = pullup_load({
            wrap: $('body'),
            group: $('#home-goods-group'),
            hintBar: $('#more-hint'),
            callback: function() {
                self_.page += 1;
                self_.get_home_data(self_.page);
            }
        });
        self_.goProm();
    },
    methods: {
        //
        get_header_data: function() {
            var self_ = this;
            $.ajax({
                type: 'GET',
                url: 'http://api.hn.pinquduo.cn/api/Index/getHeader?ajax_get=1', //
                dataType: 'jsonp',
                jsonp: 'jsoncallback',
                async: true,
                success: function(data) {
                    // console.log(data);
                    self_.header_data = data;
                    self_.$nextTick(function() {
                        nav_scroll("#cat-nav-scroll");
                    });
                    for (var i = 0; i < data.length - 1; i++) {
                        self_.cat_goods_data.goods_items.push([]);
                    };
                    // console.log(self_.cat_goods_data.goods_items);
                },
                error: function(xhr, type) {
                    console.log('Ajax error!');
                }
            });
        },
        //
        get_home_data: function(page) {
            var self_ = this;
            $.ajax({
                type: 'GET',
                url: 'http://api.hn.pinquduo.cn/api/Index/home?version=1.3.0' + '&pagesize=' + self_.pagesize + '&page=' + page + '&ajax_get=1', //
                dataType: 'jsonp',
                jsonp: 'jsoncallback',
                async: true,
                success: function(data) {
                    //console.log(data);
                    self_.home_data = data.result;
                    if (data.result.goods2.items.length < self_.pagesize) {
                        $('#more-hint').html('');
                    } else {
                        $('#more-hint').html('');
                    };
                    self_.goods_items = self_.goods_items.concat(data.result.goods2.items);

                    if (!self_.isInit) {
                        self_.$nextTick(function() {
                            setTimeout(function() {
                                focus_swipe('#focus-pic');
                                pullup_load().initLoad();
                                self_.isInit = true;
                            }, 300);
                        });
                    };
                },
                error: function(xhr, type) {
                    console.log('Ajax error!');
                }
            });
        },
        //
        get_cats: function() {
            var self_ = this;
            $.ajax({
                type: 'GET',
                url: 'http://api.hn.pinquduo.cn/api/index/getexplore&ajax_get=1', //
                dataType: 'jsonp',
                jsonp: 'jsoncallback',
                async: true,
                success: function(data) {
                    self_.cat_goods_data.cat_items = data.result.cat;
                    console.log(data);
                    $('#loading-dialog').hide();
                },
                error: function(xhr, type) {
                    console.log('Ajax error!');
                }
            });
        },
        //
        get_cats_goods: function(url, index) {
            var self_ = this;
            $.ajax({
                type: 'GET',
                url: url,
                dataType: 'jsonp',
                jsonp: 'jsoncallback',
                async: true,
                success: function(data) {
                    // console.log(data);
                    var items = data.result.items;
                    if (items.length < self_.pagesize) {
                        $('#more-hint').html('');
                    } else {
                        $('#more-hint').html('');
                    };
                    self_.$set(self_.cat_goods_data.goods_items, index, self_.cat_goods_data.goods_items[index].concat(items));
                    pullup_load().initLoad();
                },
                error: function(xhr, type) {
                    console.log('Ajax error!');
                }
            });
        },
        //
        //
        nav_fn: function(index) {
            var self_ = this;
            clearTimeout(self_.hint_timer);
            //
            if (index == 0) {
                self_.dialog_hint = 'appapp';
                $('#hint-dialog').show();
                self_.hint_timer = setTimeout(function() {
                    $('#hint-dialog').hide();
                }, 1000);
            };
            //
            if (index == 3) {
                $('#join-group').show();
            };
            //
            if (index == 4) {
                self_.dialog_hint = '...';
                $('#hint-dialog').show();
                self_.hint_timer = setTimeout(function() {
                    $('#hint-dialog').hide();
                }, 1000);
                return false;
            };

            //
            if (index == 7) {
                self_.dialog_hint = '...';
                $('#hint-dialog').show();
                self_.hint_timer = setTimeout(function() {
                    $('#hint-dialog').hide();
                }, 1000);
                return false;
            };
        },
        //
        goProm: function() {
            var self_ = this;
            var promBtn = $('#prom-btn');
            var promInput = $('#prom-input');
            promBtn.click(function() {
                clearTimeout(self_.hint_timer);
                var prom_code = promInput.val().replace(/\s/g, "");
                if (prom_code == '' || prom_code.length < 6) {
                    self_.dialog_hint = '';
                    $('#hint-dialog').show();
                    self_.hint_timer = setTimeout(function() {
                        $('#hint-dialog').hide();
                    }, 1000);
                    return;
                };
                $.ajax({
                    type: 'GET',
                    url: 'http://api.hn.pinquduo.cn/api/user/getPromDetail?invitation_num=' + prom_code + '&user_id=' + cookie.get('user_id') + '&ajax_get=1',
                    dataType: 'jsonp',
                    jsonp: 'jsoncallback',
                    async: true,
                    success: function(data) {
                        console.log(data);
                        var info = data.status == -1 ? '' : '';
                        self_.dialog_hint = info;
                        $('#hint-dialog').show();
                        hint_timer = setTimeout(function() {
                            $('#hint-dialog').hide();
                            if (data.msg) {
                                location.href = "prom_regiment.html?order_id=" + data.result.is_order.order.order_id + "&user_id=" + cookie.get('user_id');
                            };
                        }, 1000);
                    },
                    error: function(xhr, type) {
                        console.log('Ajax error!');
                    }
                });
            });

            $('#join-group .mask').click(function() {
                $('#join-group').hide();
            });
        },
        //tab
        tab: function(index, id, event) {
            var self_ = this;
            var el = $(event.currentTarget);
            console.log(el);
            $('#more-hint').html('...');
            if (index == self_.cat_index || el.hasClass('active')) {
                return;
            };

            var page = parseInt(el.attr('data-page'));
            $(window).scrollTop(0);
            //
            if (index == 0) {
                self_.pullup_load = pullup_load({
                    wrap: $('body'),
                    group: $('#home-goods-group'),
                    hintBar: $('#more-hint'),
                    callback: function() {
                        self_.page += 1;
                        self_.get_home_data(self_.page);
                    }
                });
                console.log(1);

            } else { //

                if (!el.attr('first-loaded')) {
                    self_.get_cats_goods("http://api.hn.pinquduo.cn/api/index/getNewData?page=" + page + "&ajax_get=1&id=" + id + '&pagesize=' + self_.pagesize, index - 1);
                    el.attr('first-loaded', 'true');
                };

                self_.pullup_load = pullup_load({
                    wrap: $('body'),
                    group: $('.page-goods-list').eq(index - 1),
                    hintBar: $('#more-hint'),
                    callback: function() {
                        page += 1;
                        el.attr('data-page', page);
                        self_.get_cats_goods("http://api.hn.pinquduo.cn/api/index/getNewData?page=" + page + "&ajax_get=1&id=" + id + '&pagesize=' + self_.pagesize, index - 1);
                    }
                });
            };
            self_.cat_index = index;
        },
        //
        hint_show: function(str) {
            var self_ = this;
            self_.dialog_hint = str;
            $('#hint-dialog').show();
            clearTimeout(self_.hint_timer)
            self_.hint_timer = setTimeout(function() {
                $('#hint-dialog').hide();
            }, 1000);
        }

    }
});



